.promotion {
  padding: 35px 20px;
  margin: 0;
}

.promotion__container {
  background-image: url("../img/cat-catalog.jpg");
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  flex-direction: column;
  gap: 48px;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;
  min-height: 290px;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(104, 183, 56, 0.85);
    z-index: 2;
  }
}

.promotion__text {
  @include lato(16px, 20px, 400);
  color: $white;
  text-align: center;
  margin: 0;
  max-width: 161px;
  z-index: 3;
}

.promotion__icon {
  flex-shrink: 0;
  z-index: 3;
}
